*,
*::before,
*::after {
  box-sizing: inherit; }

html {
  box-sizing: border-box; }

body {
  font: 500 12px "Roboto", sans-serif;
  color: #666;
  background-color: #f0f0f0; }

main {
  margin: 60px 0 100px; }

.container {
  margin-bottom: 2px;
  padding-right: 20px;
  padding-left: 20px;
  background: url("/images/container-bg.jpg") repeat center;
  background-size: cover; }

.avatar__user {
  margin: 0;
  border-radius: 50%;
  width: 138px;
  height: 138px;
  background-position: center;
  background-size: cover;
  transform: scale(1, 1);
  transition: 0.3s ease-out;
  transition-property: transform; }

.avatar:hover .avatar__user, .avatar:focus .avatar__user {
  transform: scale(1.05, 1.05); }

.form-label-group {
  position: relative;
  margin-bottom: 1rem; }

.form-control {
  border: none;
  border-bottom: 2px solid #333;
  letter-spacing: 1.3px;
  text-transform: uppercase; }

.contacts {
  position: relative;
  padding: 30px 0; }
  .contacts form {
    position: relative;
    margin: -8px 0 0;
    padding: 0 0 0 2px; }
  .contacts .form-group:last-child {
    margin-bottom: 0; }
  .contacts .stamp {
    position: absolute;
    right: -2px;
    bottom: 4px;
    z-index: -1;
    width: 160px;
    height: 180px;
    opacity: 0;
    transform: scale(1.2);
    transition: all 0.3s linear; }
    .contacts .stamp.is-active {
      z-index: 1;
      opacity: 1;
      animation: bounceIn 500ms;
      animation-delay: 0ms; }

@keyframes bounceIn {
  from,
  20%,
  60%,
  80%,
  to {
    transform-origin: center;
    animation-timing-function: cubic-bezier(1, 0, 1, 1); }
  0% {
    opacity: 0;
    transform: rotate(10deg) scale(2); }
  60% {
    opacity: 1;
    transform: scale(0.95); }
  80% {
    transform: scale(0.9, 0.9); }
  to {
    opacity: 1;
    transform: scale(1, 1); } }

.main-title * {
  margin: 0 0 18px;
  font-weight: 500;
  font-size: 18px;
  color: #010101; }

.custom-control {
  display: flex; }
  .custom-control-label {
    display: flex;
    align-items: center;
    letter-spacing: 1px;
    text-transform: uppercase; }

.skils {
  padding: 35px 0 0; }
  .skils .custom-control {
    margin: 0 0 35px; }
    .skils .custom-control::after {
      content: "";
      min-height: inherit;
      font-size: 0; }

.skils-level {
  padding: 50px 0 48px; }
  .skils-level__icon {
    width: 255px;
    max-width: 100%; }
  .skils-level .main-title {
