    font-size: 50px;
    line-height: 22px;
    letter-spacing: 3px;
    color: #0093d7; }

#meter__arrow {
  transition: transform 0.25s linear;
  transform-origin: 50% 84%; }

@media (max-width: 991.98px) {
  .container__contacts {
    overflow: hidden; } }

@media (max-width: 767.98px) {
